import {useState, useEffect} from 'react';
import {get, remove} from '../Calls';
import {Button, Paper, Table, TableBody, TableCell, TableRow, TableContainer, TableHead, IconButton} from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import {magazinRoute} from '../ApiRoutes';
import { useNavigate } from 'react-router-dom';

export default function MagazinList(){
    
    const [rows, setRows] = useState([]);
    const [needUpdate, setNeedUpdate] = useState(false);
    const navigate = useNavigate();

    useEffect(async () => {
        let data = await get(magazinRoute);
        setRows(data);
    }, [needUpdate]);

    const deleteMagazin = async(id, index) => {
        await remove(magazinRoute, id);

        rows.splice(index, 1);
        setRows(rows);
        setNeedUpdate(!needUpdate);
    }

    return(
        <div>

            <div>
                <Button
                    variant='contained'
                    color="primary"
                    startIcon={<AddIcon />}
                    onClick={() => {navigate("AddMagazin")}}
                >
                    Add new Magazin
                </Button>
            </div>

            <br/>

            <TableContainer component={Paper}>
                <Table aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Magazin Id</TableCell>
                            <TableCell align="right">Magazin Name</TableCell>
                            <TableCell align="right">Magazin Address</TableCell>
                            <TableCell align="right">Magazin Employee</TableCell>
                            <TableCell align="right">Magazin Phone</TableCell>
                            <TableCell align="right">Actions</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row, index) => (
                            <TableRow key={row.MagazinId}>
                                <TableCell component="th" scope="row">
                                    {row.MagazinId}
                                </TableCell>
                                <TableCell align='right'>{row.MagazinName}</TableCell>
                                <TableCell align='right'>{row.MagazinAddress}</TableCell>
                                <TableCell align='right'>{row.MagazinEmployee}</TableCell>
                                <TableCell align='right'>{row.MagazinPhone}</TableCell>
                                <TableCell align="right">
                                    <IconButton onClick={() => navigate(`/AddMagazin/${row.MagazinId}`)}>
                                        <EditIcon color="primary" />
                                    </IconButton>
                                    <IconButton onClick={() => deleteMagazin(row.MagazinId, index)}>
                                        <DeleteIcon color="secondary" />
                                    </IconButton>
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>

        </div>
    )
}