import {useState, useEffect} from 'react';
import SaveIcon from '@material-ui/icons/Save';
import CancelIcon from '@material-ui/icons/Cancel';
import {Grid, TextField, Button} from '@material-ui/core';
import {post, put, get} from '../Calls';
import {magazinRoute} from '../ApiRoutes';
import { useNavigate, useParams } from 'react-router-dom';

export default function AddMagazin(){

    const [magazin, setMagazin] = useState
    ({
        MagazinName: "",
        MagazinAddress: "",
        MagazinEmployee: 0,
        MagazinPhone: ""
    });

    const navigate = useNavigate();
    const routerParams = useParams();
    const id = routerParams.id;

    useEffect(async () => {
        if (!id)
            return;

        let data = await get(magazinRoute, id);
        setMagazin(data);    
    }, [])

    const onChangeMagazin = e => {
        setMagazin({...magazin, [e.target.name]: e.target.value});
    }

    const saveMagazin = async () => {
        if (!id)
            await post(magazinRoute, magazin);
        else
            await put(magazinRoute, id, magazin);
            
        navigate("/");    
    }

    return (
        <div>

            <Grid container spacing={3}>
                <Grid item xs={8} sm={8}>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="MagazinName"
                        name="MagazinName"
                        label="Magazin name"
                        fullWidth
                        value={magazin.MagazinName}
                        onChange={e => onChangeMagazin(e)}
                        />
                </Grid>

                <Grid item xs={4} sm={4}>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="MagazinAddress"
                        name="MagazinAddress"
                        label="Magazin address"
                        fullWidth
                        value={magazin.MagazinAddress}
                        onChange={e => onChangeMagazin(e)}
                        />
                </Grid>
                <Grid item xs={6} sm={4}>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="MagazinEmployee"
                        name="MagazinEmployee"
                        label="Magazin employee"
                        fullWidth
                        value={magazin.MagazinEmployee}
                        onChange={e => onChangeMagazin(e)}
                        />
                </Grid>
                <Grid item xs={8} sm={8}>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="MagazinPhone"
                        name="MagazinPhone"
                        label="Magazin phone"
                        fullWidth
                        value={magazin.MagazinPhone}
                        onChange={e => onChangeMagazin(e)}
                        />
                </Grid>
            </Grid>

            <Button color="primary" variant='outlined' startIcon={<CancelIcon />}
                onClick={() => {navigate("/")}}
            >
                Cancel
            </Button>  

             <Button color="primary" variant='outlined' startIcon={<SaveIcon />}
                onClick={saveMagazin}
            >
                Save
            </Button>  

        </div>
    )
}